@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
:root {
  --theme-color: #BF202F;
}

body {
  margin: 0;
  background-color: #F9F8F7 !important;
  color: #3D3D3D !important;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 600 !important;
}

@media screen and (max-width: 768px) {
  .paragraph-1 {
      font-size: 18px !important;
  }
}

main {
  max-width: 1284px;
  margin-left: auto;
  margin-right: auto;
}

.paragraph-1 {
  text-align: center;
  color: #BF202F;
  color: var(--theme-color);
  font-size: 24px;
  font-weight: bold;
}

.button-panel {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3rem;
}

.back-button {
  border: 2px solid #C9C9C9;
  background-color: #EAEAEA;
  color: #898989;
  font-size: 20px;
  font-weight: bolder;
  height: 100%;
}

.navbar {
    background-color: #FFFFFF !important;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
    margin-bottom: 1rem;
}

.nav-link.nav-link-active,
.nav-link.nav-link-active:hover,
.nav-link.nav-link-active:focus {
    color: var(--theme-color) !important;
    text-decoration: underline;
}

.greeting {
    color: #000 !important;
}
@media screen and (max-width: 767px) {
    .right-bottom-border-md {
        border-right: none !important;
        border-bottom: 1px solid var(--theme-color);
        padding-bottom: 0.5em;
        margin-bottom: 1rem;;
    }
}

.right-bottom-border-md {
    border-right: 1px solid var(--theme-color);
}

.add-inventory-panel {
    padding: 1.5rem 2rem;
    border: 3px dashed var(--theme-color);
    border-radius: 25px;
}

.add-inventory-btn {
    color: inherit !important;
    text-decoration: none !important;
}
.camera-area {
    display: flex;
    margin: 0;
    border: 3px dashed var(--theme-color);
    border-radius: 25px;
    height: 57vh;
}
@media screen and (min-width: 1284px) {
	.sidebar-control {
		display: none !important;
	}
}

.page-header {
	margin-bottom: 0.5rem;
	padding-left: 1.5rem;
}

.page-header h2 {
	color: var(--theme-color);
	font-weight: bolder;
	text-align: center;
	display: inline-block;
}

.page-body {
	display: flex;
	justify-content: flex-start;
	flex-flow: row nowrap;
	overflow: hidden;
}

.category-sidebar {
	min-width: 200px;
	height: 90vh;
	box-shadow: 2px 0 3px 0 rgba(0, 0, 0, 0.25);
	border-left: 1px solid #DDD;
	background-color: #FFF;
	margin-right: 2.5rem;
	position: relative;
	transition: all 0.5s ease;
}

.category-sidebar.hidden {
	margin-left: -200px;
	box-shadow: none;
}

.category-sidebar.hidden li.active {
	transform: none;
}

.category-sidebar.hidden .arrow-right {
	display: none;
}

.category-sidebar ul {
	width: 100%;
    display: flex;
    flex-direction: column;
   
	line-height: 0;
    list-style: none;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}

.category-sidebar ul li {
    width: 100%;
    margin-top: 1px;
	cursor: pointer;
	padding: 20px 1rem;
	transition: all .3s ease-out;
	position: relative;
}

.category-sidebar ul li:hover,
.category-sidebar ul li.active {
	transform: scale(1.05);
	border-radius: 0.1cm;
}

.category-sidebar ul li.active .arrow-right {
	height: 45px;
	width: 45px;
	position: absolute;
	right: -16px;
	bottom: -3px;
	transform: rotate(45deg);
}

.sidebar-control {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	width: 50px;
	height: 50px;
	bottom: 0.5em;
	left: 0.5em;
	background-color: var(--theme-color);
	border-radius: 50%;
	opacity: 0.75;
	cursor: pointer;
	z-index: 9999;
}

.sidebar-control.hidden {
	transform: rotate(180deg);
}

.sidebar-control img {
	width: 25px;
	height: 25px;
}

.inventory-list {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-around;
	align-content: flex-start;
}

.inventory-list .card {
	display: inline-flex;
	padding: 1rem;
	width: 200px;
	height: 250px;
	margin-right: 2rem;
	margin-bottom: 2rem;
	cursor: pointer;
	position: relative;
	box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
}

.card .cardImage {
	width: 100%;
	height: 75%;
	display: flex;
	align-items: center;
	overflow: hidden;
	margin-bottom: 3px;
}

.card .cardImage img {
	width: 100%;
	border-radius: 7%;
}

.card .card-text {
	margin: 0;
	width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}

.card .card-text.light {
	font-size: 12px;
	font-weight: normal;
}

.card .cardQty {
	display: inline-block;
	width: auto;
	position: absolute;
	bottom: 0.35rem;
	right: 0.75rem;
}

.qty {
	color: var(--theme-color);
}


